@import url(https://fonts.googleapis.com/css2?family=Krona+One&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
*{
  font-family: 'Fira Sans', sans-serif;
  color: #4A5568;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.coin-container {
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: center;
           justify-content: center;
 }

 
 .coin-row {
   display: -webkit-flex;
   display: flex;
   -webkit-flex-direction: row;
           flex-direction: row;
   -webkit-justify-content: start;
           justify-content: start;
   -webkit-align-items: center;
           align-items: center;
   height: 80px;
   border-bottom: 1px solid #d7d7d7;
   width: 900px;
   border-radius: 8px;
   box-shadow: 0px 2px 8px rgba(0,0,0,0.05);
   transition: -webkit-transform 200ms ease-in-out box-shadow 200ms ease-in-out;
   transition: transform 200ms ease-in-out box-shadow 200ms ease-in-out;
   transition: transform 200ms ease-in-out box-shadow 200ms ease-in-out, -webkit-transform 200ms ease-in-out box-shadow 200ms ease-in-out;
 }

 .coin-row:hover {
   -webkit-transform: translate(-4px);
           transform: translate(-4px);
   box-shadow: 0px 4px 16px rgba(0,0,0,0.1);;
}

 
 .coin {
   display: -webkit-flex;
   display: flex;
   -webkit-justify-content: space-between;
           justify-content: space-between;
   -webkit-align-items: center;
           align-items: center;
   min-width: 250px;
   text-align: left;
 }
 
 .coin h1 {
   font-size: 16px;
   width: 150px;
 }
 
 .coin img {
   height: 30px;
   width: 30px;
   margin-right: 10px
 }
 
 .coin-symbol {
   text-transform: uppercase;
 }
 
 .coin-data {
   display: -webkit-flex;
   display: flex;
   text-align: right;
   -webkit-justify-content: space-between;
           justify-content: space-between;
   -webkit-align-items: center;
           align-items: center;
   width: 100%;
   margin: 0 10px;
 }
 
 .coin-price {
   width: 110px;
   margin-right: 0 20px;
 }

 .coin-percent {
    margin:0  20px;
 }
 
 .coin-volume {
   width: 155px;
   margin: 0 15px;
 }
 
 .coin-marketcap {
   width: 190px;
 }

 .green {
   color: #00BFA5;
 }

 .red {
    color: #EF5350;
 }
